<template>
  <div class="m-menu">
    <dl class="nav" @mouseleave="mouseLeave">
      <dt>全部分类</dt>
      <dd v-for="(item, index) in menuList" :key="item + index" @mouseenter="mouse(item)">
        <i :class="item.icon"></i>
        {{item.name}}
        <span class="arrow"></span>
      </dd>
      
    </dl>
    <div class="detail" v-if="Detail" @mouseenter="detailEnter" @mouseleave="detailLeave">
      <template v-for="(item, index) in Detail.items">
        <h4 :key="index">{{ item.title }}</h4>
        <span v-for="(h, o) in item.items" :key="o + '_' +h">{{ h }}</span>
      </template>
    </div>
  </div>
</template>
<script>
import api from '@/api/index.js'
export default {
  data() {
    return {
      Detail: null,
      menuList: []
    };
  },
  created(){
       api.getMenu().then(res =>{
         this.menuList = res.data.data
       })
  },
  methods:{
      mouse(item){
          this.Detail = item
      },
    mouseLeave(){
        var i = this;
        this.timer = setTimeout(function(){
               this.Detail=null
        },200)

    },
    detailEnter(){
         clearTimeout(this.timer)
    },
    detailLeave(){
        this.Detail = null
    }
  }
};
</script>


