<template>
  <div class="m-product-categroy">
    <dl class="classic">
      <dt>分类</dt>
      <dt>全部</dt>
      <dd v-for="item in menuList" :key="item.type">
        <mselect :name="item.title" :list="item.subList" />
      </dd>
    </dl>
    <dl class="classic">
      <dt>区域</dt>
      <dt>全部</dt>
      <dd v-for="item in quoteList" :key="item.type">
        <mselect :name="item.title" :list="item.subList" />
      </dd>
    </dl>
  </div>
</template>
<script>
import api from "@/api/index.js";
import Mselect from "./select";
export default {
  components: {
    Mselect,
  },
  data() {
    return {
      menuList: [],
      quoteList: [
        {
          title: "推荐商圈",

          subList: [
            {
              name: "望京",

              id: 120000,
            },
            {
              name: "昌平",

              id: 12222,
            },
          ],
        },
      ],
    };
  },
  created() {
    api.getClassifyList().then((res) => {
      this.menuList = res.data.data;
    });
    // api.getAreaList().then(res =>{
    //   console.log('==',res.data)
    //   console.log('--',res.data.data)
    //   var l =JSON.parse(res)
    //   console.log('++',l)
    //   this.quoteList = l.data.data
    // })
  },
};
</script>


