import Vue from 'vue'
import Router from 'vue-router'
import defaultPage from '@/layout/default.vue'
import blankDefault from '@/layout/blank.vue'
import INdex from '@/page/index.vue'
import changeCity from '@/page/city.vue'
import goodList from "@/page/goodList.vue"
import Login from '@/page/login.vue'
import Register from '@/page/register.vue'
Vue.use(Router)

export default new Router({
  mode:'hash',
  routes: [
    {
      path: '/',
      name: 'defaultPage',
      component: defaultPage,
      redirect:'/index',
      children:[{
        path:'/index',
        name:'index',
        component:INdex
      },{
        path:'/changeCity',
        name:'changeCity',
        component:changeCity
      },{
        path:'s/:name',
        name:'goods',
        component:goodList
      }

    ]
   },
    {
      path: '/blank',
      name: 'blankDefault',
      component: blankDefault,
      children:[{
        path:'login',
        name:'login',
        component:Login
      },{
        path:'register',
        name:'register',
        component:Register
      }
    ]
    }
  ]
})



