<template>
    <div :class="['choose-wrap',disabled? 'disabled':'']" @click ='showWrapper' v-document-click ='documentClick'>
        <div class="choose">
            <span>{{value}}</span>
            <i class="el-icon-caret-bottom"></i>
            <div :class="{ 'mt-content':true,'active':showWrapperActive}">
                <h2>{{title}}</h2>
                <div :class="['wrapper',className]">
                    <div class="col" v-for="(resultlist,index) in resultList" :key='index'>
                        <span :class="{'mt-item': true,'active': item.name == value}" v-for="(item,index) in resultlist" :key='index' @click="changeValue(item)">{{item.name}}</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
      return{
      }
    },
    props:['list','value','title','showWrapperActive','disabled','className'],
    computed:{
        resultList:function(){
            let col = Math.ceil(this.list.length /12)
            let muchlist =[]
            for(var i =0; i<col;i++){
               var l = this.list.slice(i*12,i*12 + 12)
               muchlist.push(l)
            }
            return muchlist
        }
    },
    methods:{
        showWrapper(e){
            e.stopPropagation();
            if(!this.disabled){
                this.$emit('change_active',true)
            }

        },
        documentClick(){
           this.$emit('change_active',false)
        },
        changeValue(item){
            this.$emit('change',item)
        }
    }
}
</script>
<style lang="scss" >
@import '@/assets/css/changecity/select.scss';
</style>


