<template>
    <div class="page-changeCity">
          <el-row>
              <province/>
          </el-row>
           <el-row>
               <hot title="热门城市" :list = 'city'/>
           </el-row>
          <el-row>
                <hot title="最近访问" :list = 'zuicity'/>
          </el-row>
          <el-row>
              <category/>
          </el-row>
    </div>
</template>
<script>
import api from '@/api/index.js'
import Province from '@/components/changeCity/province.vue'
import hot from '@/components/changeCity/hots.vue'
import category from '@/components/changeCity/category.vue'
export default {
    data(){
     return{
           city:[],
           zuicity:[]
     }
    },
    created(){
      api.getHotCity().then(res =>{
          this.city = res.data.data.map((item) => item.name)
      })
      api.getRecent().then(res =>{
          this.zuicity = res.data.data.map((item) => item.name)
      })
    },
   components:{
       Province,
       hot,
       category
   }
}
</script>
<style lang="scss">
  @import '@/assets/css/changecity/hot.scss';
</style>


