<template>
    <div class="search-panel">
      <el-row class="m-header-searchbar">
          <el-col :span = '3' class="left">
                <img src="//s0.meituan.net/bs/fe-web-meituan/e5eeaef/img/logo.png" alt="美团">
          </el-col>
          <el-col :span = '15' class="center">
              <div class="wrapper">
                  <el-input v-model="searchWord" placeholder="请输入内容" @focus='focus' @blur="blur" @input='input' ></el-input>
                 <el-button type="primary" icon="el-icon-search">搜索</el-button>
                 <dl class="hotPlace" v-if="ishotplace">
                  <dt>热门搜索</dt>
                  <dd v-for="(item,index) in hotPlaceList" :key ="item +'_' + index">
                      <router-link :to="{name:'goods',params:{name:item}}">{{item}}</router-link>
                  </dd>
                 
              </dl>
              <dl class="searchList" v-if="searchList">
                 <dd v-for="(item,index) in isSearchList" :key ='index'>
                      <router-link :to="{name:'goods',params:{name:item}}">{{item}}</router-link>
                  </dd>
              </dl>
              </div>
              
              <p class="suggest">
                     <router-link v-for="(item, index) in isSuggest" :key="item + '~' + index" :to="{name: 'goods', params: {name: item}}">{{item}}</router-link>
                     
              </p>
          </el-col>
      </el-row>
    </div>
</template>
<script>
import api from '@/api/index.js'
export default {
    data(){
        return {
            searchWord:'',
            isFocus:false,
            hotPlaceList:[],
            isSearchList:[],
            isSuggest:[]
        }
    },
    created(){
      api.searchHotWords().then(res =>{
           this.hotPlaceList = res.data.data;
           this.isSuggest = res.data.data
       })
    },
    computed:{
       ishotplace(){
          return   this.isFocus && !this.searchWord
       },
       searchList(){
          return   this.isFocus && this.searchWord
       }
    },
    methods:{
        focus(){
            this.isFocus = true
        },
        blur(){
            var self = this;
            setTimeout(function(){
                self.isFocus = false
            },200)

        },
        input(){
            let val = this.searchWord
            api.searchWords().then(res =>{
                console.log(res)
                this.isSearchList = res.data.data.list.filter((item,index) =>{
                    return item.indexOf(val) > -1;
                })
            })
        }
    }
}
</script>



